<template>
  <v-app>
    <v-main>
      <v-container class = "bg-surface-variant">
        <v-row class="ma-2">
          <v-card outlined width="1400" ><v-card-subtitle class="text-h6 text-center">뉴미디어본부</v-card-subtitle><v-card-subtitle class="text-h4 text-center">프로젝트별 HR배치</v-card-subtitle></v-card>
        <v-spacer></v-spacer>
        <v-divider vertical></v-divider>
          <div>
            <v-col v-for= "(item) in lists" :key = "item.x">
              <v-chip class = "ms-2" :style = "{backgroundColor:name.color}" draggable v-for = "(name, idx) in item.nameList" :key="idx"
              @dragstart="startDrag($event, name)"
              >{{name.point}}<v-divider vertical></v-divider>{{name.content}}
              </v-chip>
            </v-col>
          </div>
        </v-row>
        <v-row no-gutters>
          <v-sheet class = "pa-2" width="220" >{{date}}</v-sheet>
          <v-sheet class = "text-caption pa-2" width="70" >TF회의</v-sheet>
          <v-sheet class = "text-caption pa-2" width="58" color="grey" rounded=true>장(PM)</v-sheet>
          <v-sheet class = "text-caption pa-2" width="32" >60p</v-sheet>
          <v-sheet class = "text-caption pa-2" width="30"  color="grey" rounded=true>부</v-sheet>
          <v-sheet class = "text-caption pa-2" width="50" >20p</v-sheet>
          <v-sheet class = "text-caption pa-2" width="80" >15p</v-sheet>
          <v-sheet class = "text-caption pa-2" >5p</v-sheet>
        </v-row>
        <v-sheet color="grey" rounded='xl'>
        <v-row align = "center" no-gutters>
          연구 과제
          <div class = "text-center">
            <v-col class = "ma-2" v-for="(item) in HR" :key = "item.x">
              <v-row class = "ma-2">
                <v-sheet width="110" color="grey"><input type="text" value="Project"></v-sheet>
                <v-sheet class = "pa-2" width="80" height="40" rounded="xl"><v-select label= "" :items="['월','화','수','목','금']" density = "compact"></v-select></v-sheet>          
                <div v-for="(name, idx) in  item.HRList" :key = "idx">
                <v-chip class = "ma-2" size = "large" :style = "{backgroundColor:name.color}"
                ondragover = "return false"
                @drop="onDrop($event, item.x, idx)">
                  {{name.content}}
                </v-chip>              
                </div>
              </v-row>
            </v-col>
          </div>
          <v-spacer></v-spacer>
          <v-divider vertical class = "ms-2"></v-divider>
            <v-textarea 
              variant="outlined"
              label = "이슈사항"
              clearable
              clear-icon="mdi-close-circle"
            ></v-textarea>
        </v-row>
        </v-sheet>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
  export default {
    name: 'HelloWorld',

    data: () => ({
       date : "",
       lists : [
        {
          nameList : [{content:"이소일", point:0, color:"yellow"},{content:"박정우", point:0, color : "#C5CAE9"},{content:"문예담", point:0, color : "orange"}]
        },
        {
          nameList : [{content:"최대현", point:0, color:"#B388FF"},{content:"장용수", point:0, color:"#FF5722"},{content:"김은종", point:0, color:"cyan"}]

        },
        {
          nameList : [{content:"최형규", point:0, color:"#64B5F6"},{content:"유재훈", point:0, color:"green"},{content:"김용희", point:0, color:"brown"}]
        },
        {
          nameList : [{content:"이상영", point:0, color:"#673AB7"},{content:"김도연", point:0, color:"red"},{content:"오승찬", point:0, color:"#8BC34A"}]
        }
       ],
       HR : [
        {
          x : 0, 
          projectName : "P1",
          HRList : [{content:"N U L L", value : 60, color:""},{content:"N U L L", value : 20, color:""},{content:"N U L L", value : 15, color:""},
          {content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""}]
        },
        {
          x : 1, 
          projectName : "P2",
          HRList : [{content:"N U L L", value : 60, color:""},{content:"N U L L", value : 20, color:""},{content:"N U L L", value : 15, color:""},
          {content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""}]
        },
        {
          x : 2, 
          projectName : "P3",
          HRList : [{content:"N U L L", value : 60, color:""},{content:"N U L L", value : 20, color:""},{content:"N U L L", value : 15, color:""},
          {content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""},{content:"N U L L", value : 5, color:""}]
        },
       ],
    }),
    mounted(){
      this.date = new Date().toISOString().substr(0,10);
    },
    methods: {
    startDrag(event, item) {
      event.dataTransfer.dropEffect = "copy";
      event.dataTransfer.effectAllow = "move";
      event.dataTransfer.setData("selectItem", item.content);
    },
    onDrop(event, x, y) {
      const selectedItem = event.dataTransfer.getData("selectItem")
      const preItem = this.HR[x].HRList[y].content
      this.HR[x].HRList[y].content = selectedItem
      for(let i = 0; i < 4; i++){
        for(let j = 0; j < 3; j++){
          if(this.lists[i].nameList[j].content == preItem){
            this.lists[i].nameList[j].point -= this.HR[x].HRList[y].value
          }
          if(this.lists[i].nameList[j].content == selectedItem){
            this.lists[i].nameList[j].point += this.HR[x].HRList[y].value
            this.HR[x].HRList[y].color = this.lists[i].nameList[j].color
          }
        }
      }
    },
  },
  }
</script>
